	import interfaces from '../../utils/interfaces.js';
export default {
  data() {
    return {
      sellerInfo: null
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      this.request({
      	url: interfaces.seller,
      	success: res => {
					 this.sellerInfo = res;
      	}
      });
    }
  }
};
